import {Component, EventEmitter, Inject, Input, OnChanges, OnInit, Output} from '@angular/core';
import {DefaultAssets, KonversoInterface, OpenChatBotResponse, UserInput} from '../../interface/KonversoInterface';
import {KonversoService} from '../konverso.service';
import {TranslateService} from '../translate.service';
import {
    AnimationsService,
    FadeSlideInOut,
    FadeSlideInOutKonversoOpacityOut,
    FadeSlideInOutKonversoTranslateOut,
    FadeSlideInOutKonversoHeight
} from 'ngx-nowbrains-animations';

declare var PIXI: any;
const defaultInputType = 'text';
const defaultInputLimit = 300;
const ANIMATION_TTX = 750;
const ANIMATION_TTX_ADD = 250;
const ANIMATION_SLIDE_VALUE = -50;
const ANIMATION_SLIDE_VALUE_ADD = -25;
const ANIMATION_HEIGHT_TTX = 1250;


/* TODO remove it */
import {animate, keyframes, style, transition, trigger} from '@angular/animations';

const ANIMATION_TYPE_CUBIC_BEZIER = 'cubic-bezier(0.39, 0.58, 0.57, 1)';

/* TODO */
/* move the animations to ngx-nowbrains-animations */
export const FadeSlideInOutKonversoTranslateOutTest = trigger('FadeSlideInOutKonversoTranslateOutTest', [
    transition(':enter', [
        style({opacity: 0, top: '{{percent_start}}%'}),
        animate(`{{ttx}}ms ${ANIMATION_TYPE_CUBIC_BEZIER}`, style({opacity: 1, top: '0%'}))
    ]),
    transition(':leave', [
        style({opacity: 1}),
        animate('{{ttx}}ms ease-in-out', style({opacity: 0}))
    ])
]);

export const FadeSlideInOutKonversoHeightTest = trigger('FadeSlideInOutKonversoHeightTest', [
    transition(':enter', [
        style({height: 0}),
        animate(`{{ttx}}ms ${ANIMATION_TYPE_CUBIC_BEZIER}`, style({height: '*'}))
    ]),
    transition(':leave', [
        style({opacity: 1}),
        animate('{{ttx}}ms ease-in-out', style({opacity: 0}))
    ])
]);

export const FadeSlideInOutKonversoHeightOutTest = trigger('FadeSlideInOutKonversoHeightOutTest', [
    transition(':enter', [
        style({height: '*'}),
        animate(`{{ttx}}ms ${ANIMATION_TYPE_CUBIC_BEZIER}`, style({height: 0}))
    ])
]);

/* TODO remove it */

@Component({
    selector: 'bot-full-screen',
    templateUrl: './desktop-full-screen.component.html',
    styleUrls: ['./desktop-full-screen.component.css'],
    //animations: [FadeSlideInOut, FadeSlideInOutKonversoOpacityOut, FadeSlideInOutKonversoTranslateOut, FadeSlideInOutKonversoHeight]
    animations: [FadeSlideInOut, FadeSlideInOutKonversoOpacityOut, FadeSlideInOutKonversoTranslateOut, FadeSlideInOutKonversoHeight, FadeSlideInOutKonversoTranslateOutTest, FadeSlideInOutKonversoHeightTest] // TODO remove it
})
export class DesktopFullScreenComponent implements OnChanges, OnInit {
    @Input() AssistantMode: boolean = false;
    @Input() assets: DefaultAssets;
    @Input() firstVisit: boolean = false;
    @Input() firstUsageStory: string[];
    @Input() displayData: (UserInput | OpenChatBotResponse)[];
    @Input() disableUserInput: boolean;
    @Input() LastUserInput: UserInput;
    @Input() LastBotAnswer: OpenChatBotResponse;
    @Input() PlaceHolder: string[];
    @Input() IsMobile: boolean = false;
    @Input() showInput: boolean;
    @Input() NumberPlaceHolder: string[];
    @Input() afterProcess = false;
    @Input() nbFiles = 0;

    @Output() readySend: EventEmitter<boolean> = new EventEmitter<boolean>(false);
    @Output() send: EventEmitter<UserInput> = new EventEmitter<UserInput>(null);
    @Output() sendBotCommand: EventEmitter<string> = new EventEmitter<string>(null);
    @Output() sendEvent: EventEmitter<string> = new EventEmitter<string>(null);
    @Output() sendCalendar: EventEmitter<any> = new EventEmitter<any>(null);
    @Output() openUpload: EventEmitter<boolean> = new EventEmitter<boolean>(false);
    public userInput: string;
    public currentPlaceHolder: string = '';
    public sendBtn = '';
    public sendBtnMsg = '';
    public select = '';
    public changed = false;
    public botListening = false;
    public showWrapper = true;
    public showText = true;
    public inputType: string = defaultInputType;
    public inputLimit: number = defaultInputLimit;
    private newMessage = false;
    private messageCurrent = '';
    private msgArray = [];
    private botListeningTimer = 0;
    private anim_done = false;
    private reloaded = false;
    private timer;
    public btnScreen = '';
    public nbAttachments = 0;
    config: KonversoInterface;
    private days = [];
    public calendarAvailability = {};
    public calendarTemplate = {
        morning: {all: true, monday: true, tuesday: true, wednesday: true, thursday: true, friday: true},
        afternoon: {all: true, monday: true, tuesday: true, wednesday: true, thursday: true, friday: true}
    };
    public calendarKeyOverride = {
        morning: 'MORNING_ALL',
        afternoon: 'AFTERNOON_ALL',
        monday: 'MONDAY',
        tuesday: 'TUESDAY',
        wednesday: 'WEDNESDAY',
        thursday: 'THURSDAY',
        friday: 'FRIDAY'
    };
    public showUserInputControls = true;
    public showUserInvitation = true;

    public animationTTX = ANIMATION_TTX;
    public animationTtxAdd = ANIMATION_TTX_ADD;
    public animationSlideValue = ANIMATION_SLIDE_VALUE;
    public animationSlideValueAdd = ANIMATION_SLIDE_VALUE_ADD;
    public animationHeightTTX = ANIMATION_HEIGHT_TTX;

    changeNumber(value: number) {
        if ((+this.userInput + value) > 0) {
            this.userInput = (+this.userInput + value).toString();
        }
    }

    constructor(public translate: TranslateService, public service: KonversoService, public animations: AnimationsService,
                @Inject('__NgxKonverso__') config: KonversoInterface) {
        service.lang.subscribe((r) => {
            if (service.locale) {
                this.sendBtn = translate.translate(service.locale, 'SEND');
                this.sendBtnMsg = translate.translate(service.locale, 'SEND_MESSAGE');
                this.select = translate.translate(service.locale, 'SELECT');
                this.btnScreen = translate.translate(service.locale, 'SCREENSHOT');

                this.days = [
                    'MONDAY',
                    'TUESDAY',
                    'WEDNESDAY',
                    'THURSDAY',
                    'FRIDAY',
                    'SATURDAY',
                    'SUNDAY',
                    'MONDAY',
                ];
            }
        });

        this.config = config;
    }

    ngOnChanges() {
        if (this.afterProcess) {
            this.LastUserInput = null;
            this.LastBotAnswer.text = this.config.AfterProcessScenario.BotMessage[this.service.locale];

            if (this.config.AfterProcessScenario.FormElements[0][0].label == 'Jour 1') {
                let day1 = this.days[new Date().getDay() - 1];
                let day2 = this.days[new Date().getDay()];
                let day3 = this.days[new Date().getDay() + 1];
                this.config.AfterProcessScenario.FormElements[0][0].label = day1;
                this.config.AfterProcessScenario.FormElements[1][0].label = day2;
                this.config.AfterProcessScenario.FormElements[2][0].label = day3;
            }
        }


        this.changed = false;

        if (this.LastBotAnswer && document.getElementById('text') && this.LastBotAnswer?.text && !this.LastBotAnswer?.text?.includes('loading-dots')) {
            document.getElementById('text').innerHTML = '';
        }

        if (this.LastBotAnswer && this.LastBotAnswer.text && this.LastBotAnswer?.text?.includes('<number>') && this.LastBotAnswer?.text?.includes('</number>')) {
            this.userInput = '1';
            this.inputType = 'number';
            this.inputLimit = 999;
            this.currentPlaceHolder = this.NumberPlaceHolder[Math.floor(Math.random() * this.NumberPlaceHolder.length)];
        } else {
            this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            this.inputType = defaultInputType;
            this.inputLimit = defaultInputLimit;
        }


        if (!this.anim_done) {
            let t2 = setInterval(() => {
                if (this.LastBotAnswer  && !this.LastBotAnswer?.text && !this.LastBotAnswer?.text?.includes('loading-dots') && this.anim_done) {
                    clearInterval(t2);
                    const string = this.LastBotAnswer?.text
                        .split('<br/>').join(` `)
                        .split('&eacute;').join('é')
                        .split('&egrave;').join('è')
                        .replace(/<[^>]*>?/gm, '')
                        .split('&nbsp;').join('');

                    this.msgArray = string.split('');
                    if (this.messageCurrent !== string) {
                        this.newMessage = true;
                        this.messageCurrent = string;
                        this.launchLoop();
                    }
                    //this.looper(array, timer);
                }
            }, 100);
        } else if (this.LastBotAnswer && this.LastBotAnswer.text) {
            const string = this.LastBotAnswer?.text
                .split('<br/>').join(` `)
                .split('&eacute;').join('é')
                .split('&egrave;').join('è')
                .replace(/<[^>]*>?/gm, '')
                .split('&nbsp;').join('');

            this.msgArray = string.split('');
            if (this.messageCurrent !== string && string !== '') {
                this.newMessage = true;
                this.messageCurrent = string;
                this.launchLoop();
            }
        }

        setTimeout(() => {
            this.changed = true;
        }, 100);
    }

    bindCalendar($event: any) {
        //console.log('bindCalendar $event', $event)
        delete $event.all;
        this.calendarAvailability = $event;
    }

    getCalendar() {
        this.sendCalendar.emit(this.calendarAvailability);
    }

    launchLoop() {
        this.timer = setInterval(() => {
            if (this.msgArray.length == 0) {
                clearInterval(this.timer);
            }
            if (this.newMessage) {
                if (document.getElementById('text')) {
                    document.getElementById('text').innerHTML = '';
                }

                this.newMessage = false;
                //this.msgArray = this.messageCurrent.split("");
                clearInterval(this.timer);
                this.launchLoop();
            }
            this.looper();
        }, 60);
    }

    looper() {
        if (this.msgArray.length > 0 && !this.reloaded) {
            if (document.getElementById('text')) {
                document.getElementById('text').innerHTML += this.msgArray.shift();
            }
        }
    }

    ngOnInit(): void {
        this.inputType = defaultInputType;
        this.inputLimit = defaultInputLimit;
        if (this.PlaceHolder) {
            this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            setInterval(() => {
                this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            }, 3000);
        }
    }

    userWriting(key: any) {
        if (key.code === 'Enter') {
            this.botListening = false;
            this.botListeningTimer = 0;
        } else if (key.code === 'Backspace') {

        } else {
            this.botListening = true;
            if (this.botListeningTimer == 0) {
                this.botListeningTimer += 2;
            } else if (this.botListeningTimer < 5) {
                this.botListeningTimer += 1;
            }
        }
    }

    public emit($event) {
        this.firstVisit = false;
        this.readySend.emit(true);
    }

    public _send() {
        if (this.LastBotAnswer?.endOfTopic && this.LastUserInput) {
            this.LastUserInput.message = '';
        }

        this.botListening = false;
        const userData: UserInput = {
            message: this.userInput,
            date: new Date().toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        };
        this.send.emit(userData);
        this.userInput = null;
    }

    async scroll(scrollHeight: number) {
        return new Promise<number>((resolve) => {
            setTimeout(() => {
                resolve(0);
            }, 300);
        });

    }

    byPassUserInput(botdata: string, i?: number) {
        this.sendBotCommand.emit(botdata);
    }
}
