import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {TranslateService} from '../translate.service';
import {KonversoService} from '../konverso.service';

@Component({
    selector: 'modal-add-attachments',
    templateUrl: './modal-add-attachments.component.html',
    styleUrls: ['./modal-add-attachments.component.scss']
})
export class ModalAddAttachmentsComponent implements OnInit {
    documentList: Array<any> = [];
    public input !: HTMLInputElement;
    public displayText : any = {};
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: {
            data : {input : HTMLInputElement,
            documentList : Array<any>}
        },
        public service: KonversoService,
        private dialogRef: MatDialogRef<ModalAddAttachmentsComponent>
    ) {
    }

    ngOnInit(): void {
        this.documentList = this.data.data.documentList;
        this.input = this.data.data.input;
    }

    deleteFile(file: any) {
        this.documentList.splice(this.documentList.indexOf(file), 1);
    }


    sendAttachments() {
        this.dialogRef.close( this.documentList);
    }
}
